/* src/components/product/product-list.css
   Clean merged styles for ProductList.jsx
   ------------------------------------------------------------------ */

:root {
  --brand-start: #fb923c; /* orange-400 */
  --brand-end:   #ef4444; /* red-500   */
  --muted:       #6b7280; /* gray-500  */
  --border:      rgba(17,24,39,.08);
  --shadow:      0 8px 28px rgba(17,24,39,.08);
}

/* Wrapper */
.plist {
  background: #fff;
  border-radius: 1rem;
  box-shadow: var(--shadow);
  padding: 1rem;
  margin-bottom: 2rem;
}
@media (min-width: 640px) { .plist { padding: 1.25rem; } }
@media (min-width: 768px) { .plist { padding: 1.5rem; } }

/* ---------- Controls row ---------- */
.plist__controls {
  display: grid;
  gap: 1rem;
  margin-bottom: 1.25rem;
}
@media (min-width: 992px) {
  .plist__controls { grid-template-columns: 1fr auto; align-items: center; }
}

/* ---------- Category pills ---------- */
.plist__cats {
  display: flex;
  flex-wrap: wrap;
  gap: .5rem;
}

.cat-pill {
  --ring: rgba(251,146,60,.35);
  display: inline-flex;
  align-items: center;
  gap: .45rem;
  padding: .5rem .75rem;
  background: #f3f4f6;
  color: #374151;
  border: 1px solid #e5e7eb;
  border-radius: 9999px;
  font-weight: 700;
  font-size: .9rem;
  cursor: pointer;
  transition: background .16s ease, color .16s ease,
              transform .12s ease, box-shadow .16s ease;
}
.cat-pill:hover { transform: translateY(-1px); border-color: rgba(17,24,39,.18); }
.cat-pill:focus-visible { outline: none; box-shadow: 0 0 0 3px var(--ring); }

.cat-pill.is-active {
  background: linear-gradient(90deg, var(--brand-start), var(--brand-end));
  color: #fff;
  border-color: transparent;
  box-shadow: var(--shadow);
}
.cat-pill__emoji { font-size: 1rem; }
.cat-pill__label { white-space: nowrap; }
.cat-pill__count {
  background: rgba(255,255,255,.85);
  color: #1f2937;
  padding: .1rem .45rem;
  border-radius: 9999px;
  font-size: .72rem;
  font-weight: 800;
}
.cat-pill.is-active .cat-pill__count { background: rgba(0,0,0,.2); color: #fff; }

/* ---------- Search + Sort ---------- */
.plist__searchsort {
  display: grid;
  grid-template-columns: 1fr;
  gap: .5rem;
}
@media (min-width: 520px) {
  .plist__searchsort { grid-template-columns: 1fr 220px; }
}
.plist__search,
.plist__sort {
  height: 42px;
  border-radius: .7rem;
  border: 1px solid #e5e7eb;
  background: #fff;
  padding: 0 .9rem;
  font-weight: 600;
  color: #111827;
  transition: box-shadow .16s ease, border-color .16s ease;
}
.plist__search:focus,
.plist__sort:focus {
  border-color: #fdba74;
  box-shadow: 0 0 0 3px rgba(250, 204, 21, 0.45);
}

/* ---------- Meta line ---------- */
.plist__meta {
  margin: .9rem 0 1.25rem;
  color: var(--muted);
  font-size: .9rem;
}

/* ---------- Grid ---------- */
.product-grid {
  display: grid;
  gap: 22px;
}
@media (min-width: 540px) { .product-grid { grid-template-columns: repeat(2, 1fr); } }
@media (min-width: 1024px){ .product-grid { grid-template-columns: repeat(3, 1fr); } }

/* ---------- Empty state ---------- */
.plist__empty {
  text-align: center;
  padding: 3.5rem 1rem;
  color: var(--muted);
}
.plist__emoji { font-size: 3rem; margin-bottom: .5rem; }
.plist__empty h3 { font-size: 1.35rem; font-weight: 800; margin: .25rem 0 .35rem; }
.plist__empty p { margin: 0 0 1rem; }
.plist__reset {
  background: linear-gradient(90deg, var(--brand-start), var(--brand-end));
  color: #fff;
  border: none;
  border-radius: 9999px;
  padding: .6rem 1.1rem;
  font-weight: 800;
  cursor: pointer;
  transition: transform .12s ease, filter .16s ease;
}
.plist__reset:hover { transform: translateY(-1px); filter: brightness(1.05); }


/* ---------- Page width & spacing ---------- */
.plist {
  max-width: 1120px;          /* center block */
  margin: 0 auto 2.5rem;      /* space under the block */
  padding: 1rem;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 8px 28px rgba(17,24,39,.08);
}

@media (min-width: 640px) { .plist { padding: 1.25rem; } }
@media (min-width: 768px) { .plist { padding: 1.5rem; } }

/* ---------- Controls row ---------- */
.plist__controls {
  display: grid;
  gap: 12px;
  align-items: start;
  margin-bottom: 14px;
}

@media (min-width: 900px) {
  .plist__controls {
    grid-template-columns: 1fr auto; /* pills left, search/sort right */
    align-items: center;
  }
}

/* Category pills */
.plist__cats { display:flex; flex-wrap:wrap; gap:8px; }

.cat-pill {
  --ring: rgba(251,146,60,.35);
  display:inline-flex; align-items:center; gap:8px;
  padding:10px 14px;
  border:1px solid rgba(17,24,39,.08);
  border-radius:999px;
  background:#f3f4f6; color:#374151;
  font-weight:700; font-size:.9rem; line-height:1;
  cursor:pointer;
  transition:transform .12s ease, box-shadow .16s ease, border-color .16s ease, background .16s ease, color .16s ease;
}
.cat-pill:hover{ transform:translateY(-1px); border-color:rgba(17,24,39,.18); }
.cat-pill:focus-visible{ outline:none; box-shadow:0 0 0 3px var(--ring); }
.cat-pill.is-active{
  background:linear-gradient(90deg,#fb923c,#ef4444);
  color:#fff; border-color:transparent; box-shadow:0 10px 24px rgba(0,0,0,.08);
}
.cat-pill__emoji{ font-size:16px; }
.cat-pill__label{ white-space:nowrap; }
.cat-pill__count{
  padding:2px 8px; min-width:22px; border-radius:999px; font-size:12px; font-weight:800;
  background:rgba(255,255,255,.85); color:#1f2937;
}
.cat-pill.is-active .cat-pill__count{ background:rgba(0,0,0,.2); color:#fff; }

/* Search + sort */
.plist__searchsort{ display:grid; grid-template-columns:1fr; gap:8px; }
@media (min-width:520px){ .plist__searchsort{ grid-template-columns:1fr 220px; } }

.plist__search, .plist__sort{
  height:42px; padding:0 14px; border-radius:10px;
  border:1px solid rgba(17,24,39,.1); background:#fff; color:#111827; font-weight:600;
  transition:border-color .16s ease, box-shadow .16s ease;
}
.plist__search:focus, .plist__sort:focus{
  outline:none; border-color:#fdba74; box-shadow:0 0 0 3px rgba(250,204,21,.45);
}

/* Meta line */
.plist__meta{ margin:.9rem 0 1.25rem; color:#6b7280; font-size:.9rem; }

/* ---------- Product grid (responsive 1→2→3 cols) ---------- */
.product-grid{
  display:grid;
  gap:22px;
  grid-template-columns: 1fr;          /* phones */
}
@media (min-width:540px){ .product-grid{ grid-template-columns:repeat(2, minmax(0,1fr)); } }
@media (min-width:1024px){ .product-grid{ grid-template-columns:repeat(3, minmax(0,1fr)); } }

/* Make each card equal height and the content stretch nicely */
.product-grid > *{ height:100%; display:flex; flex-direction:column; }
.product-grid > * > .p-6{ display:flex; flex-direction:column; height:100%; }
.product-grid > * > .p-6 .mt-4{ margin-top:auto; }  /* pushes timing/spice row to bottom */

/* Tidy images (works with Tailwind classes you already have) */
.product-grid img{
  width:100%;
  display:block;
  object-fit:cover;
  aspect-ratio: 16 / 10;  /* keeps cards even when images differ */
}

/* ---------- Empty state ---------- */
.plist__empty{ text-align:center; padding:56px 16px; color:#6b7280; }
.plist__emoji{ font-size:56px; margin-bottom:8px; }
.plist__empty h3{ margin:6px 0; font-size:22px; color:#111827; font-weight:800; }
.plist__reset{
  padding:10px 18px; border:none; border-radius:999px; color:#fff; font-weight:800;
  background:linear-gradient(90deg,#fb923c,#ef4444);
  box-shadow:0 10px 24px rgba(0,0,0,.08);
  cursor:pointer; transition:transform .12s ease, filter .15s ease;
}
.plist__reset:hover{ transform:translateY(-1px); filter:brightness(1.04); }
.wishlist-btn{
  display:inline-flex; align-items:center; gap:.4rem;
  padding:.45rem .75rem; border-radius:9999px;
  border:1.5px solid #e5e7eb; background:#fff; color:#374151;
  font-weight:700; font-size:.9rem; cursor:pointer;
  transition:transform .12s ease, box-shadow .15s ease, border-color .15s ease;
}
.wishlist-btn:hover{ transform:translateY(-1px); box-shadow:0 6px 16px rgba(0,0,0,.08) }
.wishlist-btn.is-wished{ border-color:#ef4444; background:#ffe4e6; color:#b91c1c }
.wishlist-btn svg{ width:16px; height:16px }
