/* ===== Products Page Styling ===== */
.products-page {
  min-height: 100vh;
  background: linear-gradient(to bottom, #fff7ed, #fef3c7);
  padding-top: 2rem;
  padding-bottom: 4rem;
}

/* Container */
.products-container {
  max-width: 80rem; /* 1280px */
  margin: 0 auto;
  padding: 0 1rem;
}

.products-header {
  text-align: center;
  margin-bottom: 2rem;
}

.products-header h1 {
  font-size: 2.5rem;
  font-weight: 800;
  color: #1f2937;
  margin-bottom: 0.5rem;
}

.products-header p {
  font-size: 1.25rem;
  color: #4b5563;
}

/* Product Grid */
.product-list {
  display: grid;
  gap: 1.5rem;
}

@media (min-width: 640px) {
  .product-list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {
  .product-list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1280px) {
  .product-list {
    grid-template-columns: repeat(4, 1fr);
  }
}
