 /* ===== Page wrapper (optional) ===== */
.products-page {
  min-height: 100vh;
  background: linear-gradient(180deg, #fff7ed 0%, #fef3c7 100%);
  padding: 2rem 0;
}

.products-container {
  max-width: 80rem;           /* ~1280px */
  margin: 0 auto;
  padding: 0 1rem;
}

.products-header {
  text-align: center;
  margin-bottom: 1.75rem;
}
.products-header h1 {
  font-size: clamp(1.8rem, 2.2vw, 2.8rem);
  font-weight: 800;
  margin: 0 0 .25rem;
  color: #0f172a;
}
.products-header p {
  color: #475569;
  font-size: clamp(1rem, 1.2vw, 1.125rem);
}

/* ===== Grid ===== */
.product-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.25rem;
}

/* ≥ 640px: 2 columns */
@media (min-width: 640px) {
  .product-grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

/* ≥ 1024px: 3 columns */
@media (min-width: 1024px) {
  .product-grid {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}

/* ===== Card ===== */
.product-card {
  position: relative;
  background: #ffffff;
  border-radius: 1rem;
  box-shadow: 0 12px 28px rgba(2, 6, 23, .08);
  overflow: hidden;
  transition: transform .18s ease, box-shadow .18s ease;
}
.product-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 16px 36px rgba(2, 6, 23, .12);
}

/* Image keeps perfect aspect and covers */
.product-card__media {
  position: relative;
  aspect-ratio: 4 / 3;        /* modern browsers */
  background: #f1f5f9;
  overflow: hidden;
}
.product-card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  transform: scale(1.02);
  transition: transform .25s ease;
}
.product-card:hover .product-card__img {
  transform: scale(1.06);
}

/* Badges (Bestseller / % OFF) */
.ps-badges-stack {
  position: absolute;
  top: .75rem;
  left: .75rem;
  display: grid;
  gap: .35rem;
}
.ps-badge {
  display: inline-flex;
  align-items: center;
  gap: .35rem;
  padding: .25rem .5rem;
  font-size: .72rem;
  font-weight: 800;
  border-radius: 9999px;
  color: #fff;
  letter-spacing: .2px;
  box-shadow: 0 6px 14px rgba(0,0,0,.18);
}
.ps-badge--hot  { background: linear-gradient(90deg, #ef4444, #f97316); }
.ps-badge--deal { background: linear-gradient(90deg, #22c55e, #16a34a); }

/* Quick-view overlay (optional) */
.product-card__overlay {
  position: absolute;
  inset: 0;
  display: grid;
  place-items: center;
  background: rgba(0, 0, 0, .35);
  opacity: 0;
  transition: opacity .2s ease;
}
.product-card:hover .product-card__overlay { opacity: 1; }
.product-card__overlay .btn-ghost {
  background: #ffffff;
  color: #111827;
  font-weight: 700;
  border-radius: 9999px;
  padding: .5rem 1rem;
}

/* Content */
.product-card__body {
  padding: 1rem;
}

.product-card__title {
  display: flex;
  align-items: center;
  gap: .5rem;
  font-weight: 800;
  color: #0f172a;
  margin: 0 0 .35rem;
  font-size: 1.05rem;
}
.ps-chip {
  width: .6rem; height: .6rem;
  border-radius: 9999px;
  border: 2px solid #fff;
  box-shadow: 0 0 0 2px #fff;
}
.ps-chip--veg    { background: #16a34a; }
.ps-chip--nonveg { background: #dc2626; }

.product-card__meta {
  display: flex;
  align-items: center;
  gap: .5rem;
  color: #64748b;
  font-size: .9rem;
  margin-bottom: .5rem;
}
.product-card__desc {
  color: #475569;
  font-size: .92rem;
  line-height: 1.4;
  margin: 0 0 .75rem;
}

/* Price row */
.ps-price {
  display: flex;
  align-items: baseline;
  gap: .5rem;
  margin-bottom: .75rem;
}
.ps-price__now {
  color: #ea580c;
  font-weight: 900;
  font-size: 1.25rem;
}
.ps-price__was {
  color: #94a3b8;
  text-decoration: line-through;
  font-weight: 600;
}

/* Spice badge + small info line */
.ps-info-line {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: .84rem;
  color: #64748b;
}
.ps-spice {
  padding: .2rem .5rem;
  border-radius: 9999px;
  font-weight: 700;
}
.ps-spice--mild   { color: #166534; background: #dcfce7; }
.ps-spice--medium { color: #92400e; background: #ffedd5; }
.ps-spice--spicy  { color: #991b1b; background: #fee2e2; }

/* Tags */
.ps-tags {
  display: flex;
  flex-wrap: wrap;
  gap: .35rem;
  margin-top: .5rem;
}
.ps-tag {
  font-size: .72rem;
  background: #f1f5f9;
  color: #334155;
  padding: .25rem .45rem;
  border-radius: 9999px;
  font-weight: 700;
}

/* Actions */
.product-card__actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: .75rem;
  margin-top: .85rem;
}
.btn-add {
  flex: 1;
  background: linear-gradient(90deg, #f97316, #ef4444);
  color: #fff;
  border: none;
  border-radius: 9999px;
  font-weight: 800;
  padding: .55rem .9rem;
  cursor: pointer;
  transition: transform .15s ease, box-shadow .15s ease;
}
.btn-add:hover { transform: translateY(-2px); box-shadow: 0 8px 22px rgba(239, 68, 68, .25); }

.icon-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px; height: 40px;
  border-radius: 9999px;
  border: 1px solid #e5e7eb;
  background: #fff;
  color: #ef4444;
  cursor: pointer;
  transition: background .15s ease, transform .15s ease;
}
.icon-btn:hover { background: #fff1f2; transform: translateY(-2px); }
.wishlist-btn {
  background: none;
  border: 1px solid #e2e8f0;
  padding: 0.4rem 0.8rem;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}
.wishlist-btn:hover {
  background: #ffe4e6;
  border-color: #f43f5e;
  color: #b91c1c;
}
